.ProductDisplay{
    display: flex;
    margin: 0 170px;
}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdislay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdislay-img-list img{
    height: 163px;
}
.productdisplay-main-img{
    width: 586px;
    height: 700px;
}
.productdisplay-right{
    margin: 0 70px;
    display: flex;
    flex-direction: column;
}


.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 600;
}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    color: #1c1c1c;
    margin-top: 13px;
    gap: 15px;
    font-size: 16px;
}
.productdisplay-prices{
    display: flex;
    margin: 40px 0;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
}
.productdisplay-old-price{
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-new-price{
    color: #ff4141;
}
.productdisplay-right-size h1{
    margin-top: 55px ;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-sizes{
    display: flex;
    gap: 20px;
    margin: 30px 0;
}
.productdisplay-sizes div{
    padding: 18px 24px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}
.productdisplay-right button{
    padding: 20px 40px;
    background: #ff4141;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
    text-transform: capitalize;

}
.productdisplay-right-category{margin-top: 10px;}
.productdisplay-right-category span{font-weight: 600;}


@media (max-width:1280px) {
    .ProductDisplay{
        margin: 0 60px;
    }
    .productdisplay-left{
        gap: 10px;
    }

    .productdislay-img-list{
        gap: 10px;
    }
    .productdislay-img-list img{
        height: 120px;
    }

    .productdisplay-main-img{
        width: auto;
        height: 510px;
    }
    .productdisplay-right{
        margin:  0 30px;
    }
    .productdisplay-right h1{
        font-size: 22px;
    }
    .productdisplay-right-stars{
        gap: 3px;
        font-size: 13px;
    }
    .productdisplay-prices{
        margin: 10px 0;
        font-size: 18px;
    }
    .productdisplay-right-description{
        font-size: 13px;
    }
    .productdisplay-right-size h1{
        margin-top: 20px;
        font-size: 20px;

    }
    .product-display-sizes div{
        padding: 14px 20px;

    }
    .productdisplay-right button{
        width: 150px;
        padding: 15px 0;
        margin-bottom: 20px;

    }
    .productdisplay-right-category{
        margin-top: 5px;
        font-size: 14px;
    }
 
}
@media (max-width:1024px) {
    .ProductDisplay{
        margin: 0 30px;
    }
    .productdisplay-left{
        gap: 5px;
    }

   
    .productdislay-img-list img{
        height: 80px;
    }

    .productdisplay-main-img{
        height: 350px;
    }
 
    .productdisplay-right h1{
        font-size: 18px;
    }
    .productdisplay-right-stars img{
     width: 15px;
    }
   
    .productdisplay-right-description{
        font-size: 12px;
    }
    .productdisplay-sizes {
        margin-top: 10px 0px;

    }
    .productdisplay-sizes div{
        padding: 10px 30px;
        font-size: 12px;

    }
    .productdisplay-right button{
        width: 120px;
        padding: 10px 0;
        margin-bottom: 10px;
        font-size: 14px;

    }
    .productdisplay-right-category{
        font-size: 12px;
    }
  
}
@media (max-width:800px) {
    .ProductDisplay{
        margin: 0 10px;
    }
    .productdisplay-left{
        gap: 0px;
        flex-direction: column-reverse;
        align-items: center;
    }

    .productdislay-img-list {
        flex-direction: row;
        gap: 6px;
        margin-top: 6px;
    }
    .productdislay-img-list img{
        height: 70px;
    }

    .productdisplay-main-img{
        height: 300px;
    }
 
    .productdisplay-right h1{
        font-size: 14px;
    }
    .productdisplay-right-stars img{
     width: 13px;
    }
   
    .productdisplay-right-description{
        font-size: 10px;
    }
    .productdisplay-sizes h1 {
        margin-top: 10px 0;

    }
    .productdisplay-sizes {
        margin-top: 10px ;
        gap: 5px;

    }
    .productdisplay-sizes div{
        padding: 4px 11px;
   

    }
    .productdisplay-right button{
        width: 100px;
        font-size: 12px;

    }
    .productdisplay-right-category{
        font-size: 10px;
    }
  
}
@media (max-width:500px) {
    .ProductDisplay{
        flex-direction: column;
    }
    .productdisplay-left{
        gap: 10px;
        flex-direction: row;
        margin: auto;
    }
    .productdislay-img-list {
        flex-direction: column;
        gap: 8px;
    }
    .productdislay-img-list img{
       height: 75px;
    }
    .productdisplay-main-img{
        height: 330px;
    }
    .productdisplay-right {
       margin: 5px;
    }
    .productdisplay-right {
        margin-top: 15px;
        font-size: 20px;
        font-weight: 500;
     }
     .productdisplay-right-stars img{
        width: 15px;
       }
       .productdisplay-sizes {
        margin: 20px 0;
        gap: 10px;

    }
    .productdisplay-sizes div{
        padding: 10px 16px;
        font-size: 14px;
   

    }
    .productdisplay-right button{
        width: 130px;
        font-size: 15px;
        padding: 12px 0;

    }
    .productdisplay-right-category{
        font-size: 16px;
    }
      
}